<template>
	<view class="countdown">
		<text>选房结束倒计时</text>
		<view class="time">
			<text  v-if="d > 0 && d != 0" class="text">{{d}}天</text>
			<text  style="margin-left: 9rpx;">{{h<10?'0'+h:h}}小时</text>
			<text>{{m<10?'0'+m:m}}分</text>
			<text>{{s<10?'0'+s:s}}秒</text>
		</view>
		<view :class="status ? 'active' : 'check' "  v-if="isShow" @click="changeStatus" >
			只看未选
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			startTime: {
				type: String,
			},
			endTime:{
				type: String,
			},
			isShow:{
				type: String,
			}
		},
		data(){
			return{
				timer:null,
				d:0,
				h:0,
				m:0,
				s:0,
				status: false
			}
		},
		mounted() {
			this.time()
		},
		beforeDestroy(){
			clearInterval(this.timer);
			this.timer = null;
		},
		computed:{
			sTime(){return this.startTime},
			eTime(){return this.endTime}
		},
		watch:{
			endTime(){
				clearInterval(this.timer);
				this.time()
			}
		},
		methods:{ 
			time(){
				let leftTime = this.GetDateDiff(this.sTime,this.eTime)
				this.getCountdownTime(leftTime)
			},
			//计算两个时间差
			GetDateDiff(startTime, endTime) {
			    //将xxxx-xx-xx的时间格式，转换为 xxxx/xx/xx的格式
			    startTime = startTime.replace(/\-/g, "/");
			    endTime = endTime.replace(/\-/g, "/");
			    //将计算间隔类性字符转换为小写
			    var sTime = new Date(startTime); 
			    var eTime = new Date(endTime); 
			    return parseInt((eTime.getTime() - sTime.getTime()) / 1000);
			},
			//计算活动结束时间
			getCountdownTime(leftTime){
				let time = leftTime
				if (time>0) {
					this.timer = setInterval(() => {
						if(time == 0){
							clearInterval(this.timer)
							this.h = 0
							this.m = 0
							this.s = 0
						}else{
							this.d = parseInt(leftTime/3600/24);
							this.h = parseInt((time/3600)%24);
							this.m = parseInt((time/60)%60);
							this.s = parseInt(time%60);
							time --
						}
					},1000)
				}
			},
			changeStatus(e) {
				this.status = !this.status
				this.$emit("getChild",this.status);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.countdown {
		transition: all 0.3;
		box-sizing: border-box;
		position: relative;
		top: 0;
		left: 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 40rpx;
		width: 100vw;
		height: 80rpx;
		background: rgba(38, 135, 251, 0.2);
		font-size: 28rpx;
		color: #000000;
	
		.time {
			margin-left: 5rpx;
			font-size: 32rpx;
			flex: 1;
			color: #6337CC;
		}
	
		.check {
			width: 134rpx;
			height: 52rpx;
			border-radius: 10rpx;
			border: 2rpx solid #717171;
			font-size: 28rpx;
			color: #717171;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	
		.active {
			color: #6337CC;
			border: 2rpx solid #6337CC;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 134rpx;
			height: 52rpx;
			border-radius: 10rpx;
			font-size: 28rpx;
		}
	}
</style>
